'use client';

import Image from 'next/image';
import Link from 'next/link';

export default function Header() {
    return (
        <header className="bg-white border-b border-gray-200 dark:bg-gray-900 dark:border-gray-700">
            <div className="max-w-7xl mx-auto px-4 py-4">
                <div className="flex flex-col lg:flex-row items-center justify-between gap-4">
                    {/* Left Logos - Partner Organizations */}
                    <div className="flex items-center gap-4 lg:gap-6">
                        <Link href="https://um.dk/en" target="_blank" rel="noopener noreferrer" className="shrink-0">
                            <Image
                                src="/denmark.png"
                                alt="Denmark Ministry of Foreign Affairs"
                                width={140}
                                height={50}
                                className="object-contain h-12 lg:h-14 w-auto"
                                priority
                            />
                        </Link>
                        <div className="w-px h-10 bg-gray-300 hidden sm:block" />
                        <Link href="https://jalshakti.gov.in/" target="_blank" rel="noopener noreferrer" className="shrink-0">
                            <Image
                                src="/Ministry_of_Jal_Shakti.svg"
                                alt="Ministry of Jal Shakti"
                                width={80}
                                height={50}
                                className="object-contain h-12 lg:h-14 w-auto"
                                priority
                            />
                        </Link>
                    </div>

                    {/* Center Title */}
                    <div className="text-center flex-1 px-4">
                        <h1 className="text-xl sm:text-2xl lg:text-3xl font-bold text-gov-primary dark:text-white tracking-tight">
                            Smart Laboratory on Clean Rivers
                        </h1>
                        <p className="text-xs sm:text-sm text-gray-600 dark:text-gray-400 mt-0.5">
                            Indo-Danish Water Technology Alliance
                        </p>
                    </div>

                    {/* Right Logos - SLCR & Namami Gange */}
                    <div className="flex items-center gap-4 lg:gap-6">
                        <Link href="/" className="shrink-0">
                            <Image
                                src="/Logo_edited.png"
                                alt="SLCR Logo"
                                width={80}
                                height={50}
                                className="object-contain h-12 lg:h-14 w-auto"
                                priority
                            />
                        </Link>
                        <div className="w-px h-10 bg-gray-300 hidden sm:block" />
                        <Link href="https://nmcg.nic.in/" target="_blank" rel="noopener noreferrer" className="shrink-0">
                            <Image
                                src="/namiti_gange.gif"
                                alt="Namami Gange Programme"
                                width={60}
                                height={50}
                                className="object-contain h-12 lg:h-14 w-auto"
                                priority
                            />
                        </Link>
                    </div>
                </div>
            </div>
        </header>
    );
}
