'use client';

import { useState, useEffect, useRef } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { ChevronDown, Menu, X } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

interface NavItem {
    label: string;
    href: string;
    children?: NavItem[];
}

const navItems: NavItem[] = [
    { label: 'Home', href: '/' },
    {
        label: 'About',
        href: '/about',
        children: [
            { label: 'About SLCR', href: '/about/slcr' },
            { label: 'Partner Institutes', href: '/about/partner' },
            { label: 'Secretariat', href: '/secretariat' },
        ],
    },
    {
        label: 'Projects',
        href: '/projects',
        children: [
            { label: 'DSS-WRM', href: '/projects/project1' },
            { label: 'Fingerprint Analysis', href: '/projects/project2' },
            { label: 'Hydrological Modelling', href: '/projects/project3' },
        ],
    },
    {
        label: 'Activities',
        href: '/activities',
        children: [
            { label: 'RHAR 2025', href: '/activities/rhar' },
            { label: "People's Varuna", href: '/activities/peoples_varuna' },
        ],
    },
    { label: 'Technology Forum', href: '/technology' },
    { label: 'Data', href: '/data' },
    { label: 'Gallery', href: '/media' },
    { label: 'Contact', href: '/contact' },
];

export default function MainNav() {
    const [openDropdown, setOpenDropdown] = useState<string | null>(null);
    const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
    const [isScrolled, setIsScrolled] = useState(false);
    const pathname = usePathname();
    const navRef = useRef<HTMLElement>(null);

    // Handle scroll for sticky effect
    useEffect(() => {
        const handleScroll = () => {
            setIsScrolled(window.scrollY > 100);
        };
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    // Close dropdown when clicking outside
    useEffect(() => {
        const handleClickOutside = (e: MouseEvent) => {
            if (navRef.current && !navRef.current.contains(e.target as Node)) {
                setOpenDropdown(null);
            }
        };
        document.addEventListener('mousedown', handleClickOutside);
        return () => document.removeEventListener('mousedown', handleClickOutside);
    }, []);

    // Close mobile menu on route change
    useEffect(() => {
        setMobileMenuOpen(false);
        setOpenDropdown(null);
    }, [pathname]);

    // Handle keyboard navigation
    const handleKeyDown = (e: React.KeyboardEvent, item: NavItem) => {
        if (e.key === 'Escape') {
            setOpenDropdown(null);
        } else if (e.key === 'Enter' || e.key === ' ') {
            if (item.children) {
                e.preventDefault();
                setOpenDropdown(openDropdown === item.label ? null : item.label);
            }
        }
    };

    const isActive = (href: string) => {
        if (href === '/') return pathname === '/';
        return pathname.startsWith(href);
    };

    return (
        <nav
            ref={navRef}
            className={`bg-gov-primary dark:bg-gray-800 transition-all duration-300 ${isScrolled ? 'fixed top-0 left-0 right-0 z-50 shadow-lg' : ''
                }`}
        >
            <div className="max-w-7xl mx-auto px-4">
                {/* Desktop Navigation */}
                <div className="hidden lg:flex items-center justify-center">
                    {navItems.map((item) => (
                        <div key={item.label} className="relative group">
                            {item.children ? (
                                <button
                                    onClick={() => setOpenDropdown(openDropdown === item.label ? null : item.label)}
                                    onKeyDown={(e) => handleKeyDown(e, item)}
                                    onMouseEnter={() => setOpenDropdown(item.label)}
                                    onMouseLeave={() => setOpenDropdown(null)}
                                    className={`flex items-center gap-1 px-4 py-3.5 text-white font-medium transition-all hover:bg-white/10 focus:outline-none focus:ring-2 focus:ring-white focus:ring-inset ${isActive(item.href) ? 'bg-white/15 border-b-2 border-gov-saffron' : ''
                                        }`}
                                    aria-expanded={openDropdown === item.label}
                                    aria-haspopup="true"
                                >
                                    {item.label}
                                    <ChevronDown
                                        size={16}
                                        className={`transition-transform duration-200 ${openDropdown === item.label ? 'rotate-180' : ''
                                            }`}
                                    />
                                </button>
                            ) : (
                                <Link
                                    href={item.href}
                                    className={`block px-4 py-3.5 text-white font-medium transition-all hover:bg-white/10 focus:outline-none focus:ring-2 focus:ring-white focus:ring-inset ${isActive(item.href) ? 'bg-white/15 border-b-2 border-gov-saffron' : ''
                                        }`}
                                >
                                    {item.label}
                                </Link>
                            )}

                            {/* Dropdown Menu */}
                            {item.children && (
                                <AnimatePresence>
                                    {openDropdown === item.label && (
                                        <motion.div
                                            initial={{ opacity: 0, y: -5 }}
                                            animate={{ opacity: 1, y: 0 }}
                                            exit={{ opacity: 0, y: -5 }}
                                            transition={{ duration: 0.15 }}
                                            onMouseEnter={() => setOpenDropdown(item.label)}
                                            onMouseLeave={() => setOpenDropdown(null)}
                                            className="absolute left-0 top-full w-56 bg-white dark:bg-gray-800 shadow-xl rounded-b-lg border border-gray-200 dark:border-gray-700 z-50 overflow-hidden"
                                        >
                                            {item.children.map((child, idx) => (
                                                <Link
                                                    key={child.href}
                                                    href={child.href}
                                                    className={`block px-4 py-3 text-gray-700 dark:text-gray-200 hover:bg-gov-primary hover:text-white transition-colors border-l-3 border-transparent hover:border-gov-saffron ${idx !== item.children!.length - 1 ? 'border-b border-gray-100 dark:border-gray-700' : ''
                                                        } ${isActive(child.href) ? 'bg-gov-primary/10 border-l-gov-saffron' : ''}`}
                                                >
                                                    {child.label}
                                                </Link>
                                            ))}
                                        </motion.div>
                                    )}
                                </AnimatePresence>
                            )}
                        </div>
                    ))}
                </div>

                {/* Mobile Menu Button */}
                <div className="lg:hidden flex items-center justify-between py-3">
                    <span className="text-white font-medium">Menu</span>
                    <button
                        onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
                        className="p-2 text-white hover:bg-white/10 rounded-lg focus:outline-none focus:ring-2 focus:ring-white"
                        aria-label={mobileMenuOpen ? 'Close menu' : 'Open menu'}
                        aria-expanded={mobileMenuOpen}
                    >
                        {mobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
                    </button>
                </div>
            </div>

            {/* Mobile Menu Drawer */}
            <AnimatePresence>
                {mobileMenuOpen && (
                    <motion.div
                        initial={{ height: 0, opacity: 0 }}
                        animate={{ height: 'auto', opacity: 1 }}
                        exit={{ height: 0, opacity: 0 }}
                        transition={{ duration: 0.2 }}
                        className="lg:hidden bg-white dark:bg-gray-800 border-t border-gray-200 dark:border-gray-700 overflow-hidden"
                    >
                        <div className="max-h-[70vh] overflow-y-auto">
                            {navItems.map((item) => (
                                <div key={item.label} className="border-b border-gray-100 dark:border-gray-700 last:border-0">
                                    {item.children ? (
                                        <>
                                            <button
                                                onClick={() => setOpenDropdown(openDropdown === item.label ? null : item.label)}
                                                className={`w-full flex items-center justify-between px-4 py-3 text-left font-medium ${isActive(item.href)
                                                        ? 'text-gov-primary bg-gov-primary/5'
                                                        : 'text-gray-700 dark:text-gray-200'
                                                    }`}
                                            >
                                                {item.label}
                                                <ChevronDown
                                                    size={18}
                                                    className={`transition-transform duration-200 ${openDropdown === item.label ? 'rotate-180' : ''
                                                        }`}
                                                />
                                            </button>
                                            <AnimatePresence>
                                                {openDropdown === item.label && (
                                                    <motion.div
                                                        initial={{ height: 0 }}
                                                        animate={{ height: 'auto' }}
                                                        exit={{ height: 0 }}
                                                        className="overflow-hidden bg-gray-50 dark:bg-gray-900"
                                                    >
                                                        {item.children.map((child) => (
                                                            <Link
                                                                key={child.href}
                                                                href={child.href}
                                                                className={`block px-8 py-2.5 text-sm border-l-3 ${isActive(child.href)
                                                                        ? 'text-gov-primary border-gov-saffron bg-white dark:bg-gray-800'
                                                                        : 'text-gray-600 dark:text-gray-400 border-transparent hover:border-gov-primary'
                                                                    }`}
                                                            >
                                                                {child.label}
                                                            </Link>
                                                        ))}
                                                    </motion.div>
                                                )}
                                            </AnimatePresence>
                                        </>
                                    ) : (
                                        <Link
                                            href={item.href}
                                            className={`block px-4 py-3 font-medium ${isActive(item.href)
                                                    ? 'text-gov-primary bg-gov-primary/5 border-l-3 border-gov-saffron'
                                                    : 'text-gray-700 dark:text-gray-200'
                                                }`}
                                        >
                                            {item.label}
                                        </Link>
                                    )}
                                </div>
                            ))}
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </nav>
    );
}
